.mainpage{
    display: flex;
    flex-direction: column;
    height: 100vh;
    justify-content: center;
    align-items: center;
    gap: 1em;
}
.mainpage div{
    display: grid;
    grid-template-columns: 3em 15em;
    align-items: center;
}
.mainpage select{
    padding: 1em 2em;
    cursor: pointer;
    outline: none;
}
.mainpage button{
    padding:1.5em 9.5em;
    cursor: pointer;
    border: none;
    border-radius: 0.25em;

}