.navbar{
    box-sizing: border-box;
    position: absolute;
    display: flex;
    justify-content: space-between;
    align-items: center;
    box-shadow: 1px 1px 5px silver;
    width: 100%;
    padding: 1em;
}
.navbar--left{
    display: flex;
    gap: 1em;
}
.navbar--left--profile{
    display: flex;
    align-items: center;
    cursor: pointer;
}
.navbar--left--users{
    position: absolute;
    right: 0;
    top: 3.5em;
    padding:1em;
    list-style: none;
    background-color: gainsboro;
    color: black;
    z-index: -5;
} 
li:hover{
    background-color: orange;
}
.navbar--left--users li{
    padding:0.75em 1em;
}
.navbar--image{
    width: 1em;
    height: 1em;
    padding-inline:0.5em;
}